.map {
  // width: 100%;

  @include breakpoint(xl) {
    margin: 0 -15px 0;
    height: 338px;
  }

  @include breakpoint(lg) {
    width: 100vw;
    margin-top: 20px;
  }

  &__title {
    @include fontSize(26px);
    margin-bottom: 25px;

    @include breakpoint(xl) {
      padding: 0 15px;
      @include fontSize(18px);
      font-weight: 600;
      margin-bottom: 20px;
    }
  }

  & > ymaps {
    height: 300px!important;
    border-radius: 10px;
    overflow: hidden;

    @include breakpoint(xl) {
      border-radius: 0;
      width: calc(100vw - 17px)!important;
      position: absolute;
      left: 0;
    }

    @include breakpoint(lg) {
      width: 100%!important;
    }

    & > ymaps {
      width: 100%!important;

      @include breakpoint(xl) {
        width: calc(100vw - 17px)!important;
      }

      @include breakpoint(lg) {
        width: 100%!important;
      }

      & > ymaps {
        width: 100%!important;

        @include breakpoint(xl) {
          width: calc(100vw - 17px)!important;
        }
        
        @include breakpoint(lg) {
          width: 100%!important;
        }
      }
    }
  } 
}