.breadcrumbs {
  padding: 0 0 8px 0;
  margin: 0;
  list-style: none;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  
  @include breakpoint(xl) {
    padding: 0;
  }

  &__mobile-button {
    display: none;
    width: 100%;
    font-weight: 500;
    position: absolute;
    top: 44px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 13px;

    &:before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: -1;
      filter: contrast(15%) brightness(210%) saturate(45%);
      background: var(--main-500);
      color: var(--main-500);
    }

    @include breakpoint(xl) {
      display: flex;
    }

    a {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 15px;
      color: var(--main-500);
    }

    .icon {
      width: 13px;
      height: 12px;
      margin-right: 10px;
    }
  }

  &__item {
    padding-right: 16px;
    position: relative;

    @include breakpoint(xl) {
      display: none;
    }

    a {
      color: $darkgrey;
      pointer-events: none;
    }

    &:not(:last-of-type):after {
      position: absolute;
      content: '';
      width: 4px;
      height: 4px;
      transform: translateY(-50%) rotate(-45deg);
      top: 50%;
      right: 7px;
      border-bottom: 1px solid #99A2AD;
      border-right: 1px solid #99A2AD;
    }

    &_home {
      a {
        pointer-events: all;
      }

      .icon {
        width: 20px;
        height: 20px;
      }
    }
    
    &.active a {
      pointer-events: all;
      color: var(--main-500);

      &:hover {
        color: var(--button-primary);
      }
    }
  }
}