.amount {
  display: flex;

  &__input {
    margin: 0 4px 0 0;
    border: 1px solid $lightgrey;
    border-radius: 5px;
    padding: 7px 3px;
    text-align: center;    
    max-width: 40px;
    min-width: 40px;
    width: 100%;
  }

  &__plus, &__minus {
    position: relative;
    min-width: 36px;
    max-width: 36px;
    height: 36px;
    border-radius: 5px;
    color: $white;
    background: var(--button-primary);

    &:hover {
      background-color: var(--button-primary-light);
    }
  
    &:active {
      background-color: var(--button-primary-dark);
    }
  }

  &__minus:before {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    content: '';
    position: absolute;
    width: 12px;
    height: 2px;
    background-color: $white;
  }

  &__plus {
    &:before {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      content: '';
      position: absolute;
      width: 12px;
      height: 2px;
      background-color: $white;
    }

    &:after {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      content: '';
      position: absolute;
      width: 2px;
      height: 12px;
      background-color: $white;
    }
  }
  
  &__type {
    align-self: center;
    font-size: 15px;
  }
}