.cart {
  margin-top: 20px;

  @include breakpoint(xl) {
    margin-top: 0;
    margin-bottom: 30px;
  }

  &__buttons {
    margin-top: 40px;
    display: flex;

    @include breakpoint(xl) {
      flex-direction: column-reverse;
      margin-top: 20px;
      align-items: center;
    }
  }

  &__button {
    max-width: 200px;

    @include breakpoint(xl) {
      max-width: 100%;
    }

    &:not(:first-of-type) {
      margin-left: 20px;
      
      @include breakpoint(xl) {
        margin-left: 0;
        margin-bottom: 20px;
      }
    }
  }
}