.footer {
  @include breakpoint(xl) {
    background-color: var(--main-500);
  }

  &__inner {
    padding-top: 2px;
    padding-bottom: 2px;
    display: flex;
    align-items: center;
    margin-left: 0;
    line-height: 20px;

    @include breakpoint(xl) {
      flex-direction: column;
      align-items: flex-start;
      
      &:nth-child(1) {
        order: 3;
        margin-bottom: 40px;
        flex-direction: row;
        padding: 0;
        margin-left: 20px;

        .footer__messengers {
          margin: 0;
          margin-left: 20px;
        }
      }

      &:nth-child(2) {
        order: 1;
        margin-bottom: 16px;
      }

      &:nth-child(3) {
        order: 3;
        margin-bottom: 40px;
        flex: 1 1 100%;
      }

      &:nth-child(4) {
        order: 4;
      }
    }

    @include breakpoint(lg) {
      flex: 1 1 100%;

      &:nth-child(1) {
        margin-left: 0;
        flex-direction: column;
        margin-bottom: 0px;
        padding-bottom: 40px;

        .footer__messengers {
          margin-left: 0;
          margin-top: 17px;
        }
      }

      &:nth-child(2) {
        margin-right: 20px;
      }
    }

    &:nth-child(2) {
      border-left: 1px solid rgba(255, 255, 255, 0.2);
      padding-left: 18px;
      margin-left: 18px;
      @include breakpoint(xl) {
        border: none;
        padding: 0;
        margin-left: 0;
      }
    }

    &:nth-child(3) {
      border-left: 1px solid rgba(255, 255, 255, 0.2);
      padding-left: 22px;
      margin-left: 20px;
      @include breakpoint(xl) {
        border: none;
        padding: 0;
        margin-left: 0;
      }
    }

    &:nth-child(4) {
      padding-left: 15px;
      margin-left: auto;
      @include breakpoint(xl) {
        border: none;
        padding: 0;
        margin-left: 0;
      }
    }
  }

  &__address {
    display: flex;
    font-size: 11px;

    @include breakpoint(xl) {
      flex-direction: column;
    }

    div:first-child {
      margin-right: 13px;
    }
  }

  &__contacts {
    width: 100%;
    border-radius: 5px;
    display: flex;
    padding: 24px 16px;
    color: $white;
    background-color: var(--main-500);

    @include breakpoint(xl) {
      flex-direction: column;
      align-items: flex-start;
      padding: 20px 17px;
      margin: 0 -15px;
      width: calc(100% + 30px);
      border-radius: 0;
    }

    @include breakpoint(xl) {
      flex-direction: row;
      flex-wrap: wrap;
    }

    @include breakpoint(lg) {
      flex-direction: row;
      flex-wrap: wrap;
    }

    a {
      &:hover {
        color: #fff;
        text-decoration: underline;
        
        svg {
          fill: #fff
        }
      }
    }
  }

  &__messengers {
    display: flex;
    align-items: center;
    padding: 0 0 0 20px;

    @include breakpoint(xl) {
      padding: 0;
      margin-top: 16px;
    }

    a {
      width: 17px;
      height: 17px;

      @include breakpoint(xl) {
        width: 20px;
        height: 20px;
      }

      &:not(:first-of-type) {
        margin-left: 18px;
      }
    }
  }

  &__tel {
    @include fontSize(17px);
    color: $white;
  }

  &__email {
    display: flex;
    align-items: center;
    color: $white;
    .icon {
      width: 24px;
      height: 24px;
      margin-right: 8px;
    }
  }

  &__menu {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-height: 120px;
    list-style: none;
    padding: 0 16px;
    margin: 20px 0 0 0;

    @include breakpoint(xl) {
      display: none;
    }

    li {
      margin-bottom: 20px;
    }
  }

  &__copyright {
    width: 100%;
    color: $darkgrey;
    padding-top: 20px;
    padding-bottom: 22px;
    border-top: 1px solid $lightgrey;
    position: relative;
    
    @include breakpoint(xl) {
      border: none;
      margin: 0 -15px;
      width: calc(100% + 30px);
      background-color: var(--main-500);
      padding: 16px;
      color: $white;
    }

    &:before {
      display: none;

      @include breakpoint(xl) {
        display: block;
        content: '';
        position: absolute;
        top: 0;
        left: 15px;
        height: 1px;
        width: calc(100% - 30px);
        background-color: var(--main-900);
      }
    }
  }
}