.slider-wrap {
  width: 100%;
  .swiper-slide {
    overflow: hidden;
    img {
      object-fit: cover;
      width: 100%;
    }
  }
}

.slider-top, .slider-top-disabled {
  background-color: #F0F3F8;
  border: 1px solid #E1E3E6;

  @include breakpoint(md) {
    border: none;
  }

  &__slide {
    height: 273px;
    width: 100%!important;
    @include breakpoint(xl) {
      background-size: contain;
      height: 250px;
    }
  }

  &__zoom {
    width: 46px;
    height: 46px;
    background: rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 6px;
    right: 6px;
    z-index: 10;
    cursor: pointer;

    .icon {
      width: 17.5px;
      height: 17.5px;
    }
  }
}

.swiper-pagination {
  opacity: 0;
  visibility: hidden;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  width: auto!important;
  left: 50%!important;
  transform: translateX(-50%);
  padding: 5px;

  .swiper-pagination-bullet {
    background-color: #fff;
    opacity: 1;
  }

  @include breakpoint(xl) {
    opacity: 1;
    visibility: visible;
  }

  .swiper-pagination-bullet-active {
    background-color: var(--main-500);
  }
}

.slider-thumbs {
  margin-top: 10px;

  @include breakpoint(xl) {
    margin-top: -100px;
    z-index: -1;
    max-width: 260px;
  }

  .swiper-slide {
    height: 88px;
    width: 135px;
    opacity: 0.4;
    border: 1px solid #EBEBEB;
    border-radius: 3px;
  }

  .swiper-slide-thumb-active {
    opacity: 1;
  }
}

.modal-slider {
  width: 100%;
  height: 100%;

  @include breakpoint(xl) {
    width: calc(100% + 20px);
    height: 100%;
    margin: 0 -10px;
  }

  .swiper-wrapper {
    @include breakpoint(xl) {
      height: 40vh;
    }
  }

  .swiper-slide {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border-radius: 5px;
    overflow: hidden;

    @include breakpoint(xl) {
      border-radius: 0;
    }
  }

  ~ .swiper-button-prev, + .swiper-button-next {
    background: rgba(0, 0, 0, 0.3);
    border-radius: 50px;
    width: 76px;
    height: 76px;
    display: flex;
    align-items: center;
    justify-content: center;

    &:before {
      content: '';
      position: absolute;
      width: 14px;
      height: 24px;
      background: url(../img/arrow-swiper.svg) no-repeat;
    }

    &:active, &:focus {
      outline: none;
    }
  }

  ~ .swiper-button-prev {
    left: -110px;
    &:before {
      transform: rotate(180deg);
      margin-left: -3px;
    }
  }

  ~ .swiper-button-next {
    right: -110px;
    &:before {
      margin-left: 3px;
    }
  }
  
  .swiper-pagination {
    position: fixed;
  }
}