.side-button {
  $self: &;

  position: fixed;
  right: -81px;
  top: 50%;
  overflow: hidden;
  background-color: var(--main-500);
  color: #fff;
  z-index: 50;
  line-height: 18px;
  font-size: 13px;
  height: 42px;
  width: 204px;
  box-shadow: -4px 0px 10px rgba(0, 0, 0, 0.5);
  border-radius: 5px 5px 0px 0px;
  transform: rotate(-90deg);
  display: flex;
  align-items: center;
  justify-content: center;

  .icon {
    width: 18px;
    height: 18px;
    fill: #fff;
    margin-right: 10px;
  }

  &-mobile {
    position: fixed;
    right: 0px;
    bottom: 0px;
    overflow: hidden;
    z-index: 50;
    background-color: var(--main-500);
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 62px;
    width: 62px;
    border-radius: 40px;
    margin-bottom: 12px;
    margin-right: 12px;
    box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.09), 0px 2px 3px rgba(0, 0, 0, 0.1), 0px 2px 15px rgba(0, 0, 0, 0.1);

    .icon {
      width: 28px;
      height: 28px;
      fill: #fff;
    }

  }
}