.modal {
  $self: &;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 11111;
  display: none;

  &__wrap {
    position: relative;
    max-width: 1100px;
    width: 100%;
    height: 100%;
    max-height: 80vh;
    z-index: 11000;
    margin: 20px 130px;
    display: none;

    @include breakpoint(xl) {
      margin: 20px 10px;
      height: auto;
    }
  }

  &__content {
    padding: 60px 30px;
    overflow: auto;
    height: auto;
    width: 100%;
    border: 1px solid #E1E3E6;
    box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    border-radius: 5px;
    text-align: center;

    @include breakpoint(xl) {
      padding: 24px 15px;
    }
  }

  &__overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 10000;
    backdrop-filter: blur(2px);
    background-color: #141414;
    opacity: 0.9;
    display: none;
  }

  &__close {
    display: flex;
    align-items: center;
    cursor: pointer;
    position: absolute;
    top: -60px;
    right: 0;
    width: 120px;
    height: 46px;
    background: rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    padding-left: 50px;
    color: $white;

    &:before {
      top: 50%;
      left: 15px;
      transform: translateY(-50%) rotate(45deg);
      content: '';
      position: absolute;
      width: 14px;
      height: 2px;
      background-color: $white;
    }

    &:after {
      top: 50%;
      left: 21px;
      transform: translateY(-50%) rotate(45deg);
      content: '';
      position: absolute;
      width: 2px;
      height: 14px;
      background-color: $white;
    }
  }

  &_main {
    #{$self}__wrap {
      max-width: 530px;
      height: auto;
    }
  }
  
  [data-modal=contacts] {
    .header__buttons::before {
      display: none;
    }
  }
}

@media (max-width: 568px){
  .modal_main .modal__wrap{
    margin: 20px 10px;
  }
}