.link-list {
  width: 100%;
  padding: 0;
  column-count: 4;
  column-gap: 10px;
  list-style: none;
  margin: 0;

  @include breakpoint(xl) {
    column-count: 2;
    margin-bottom: 0;
  }

  @include breakpoint(lg) {
    column-count: 1;
    margin-bottom: 0;
  }

  + .link-list-title {
    margin: 16px 0 8px;
  }
  
  &-title {
    @include fontSize(17px);
    line-height: 38px;
    font-weight: bold;
    margin-bottom: 8px;
    margin-top: 17px;
  }

  &__title {
    @include fontSize(20px);
    font-weight: 500;
    margin-bottom: 40px;

    @include breakpoint(xl) {
      @include fontSize(16px);
      margin-bottom: 24px;
    }
  }

  &__item {
    position: relative;
    display: block;
    @include fontSize(14px);

    &:before {
      position: absolute;
      content: '';
      width: 5px;
      height: 100%;
      background-color: #F0F3F8;
      top: 0;
      left: 0;
    }
  }

  &__wrap {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  &__subname {
    margin-top: 3px;
    color: $darkgrey;
  }

  &__img {
    margin-right: 12px;
    width: 80px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;

    @include breakpoint(xl) {
      width: 90px;
      height: 45px;
    }

    img {
      max-height: 40px;
      max-width: 80px;

      @include breakpoint(xl) {
        max-width: 90px;
        max-height: 45px;
      }
    }
  }
  
  a {
    break-inside: avoid-column;
    overflow: hidden;
    width: 100%;
    min-height: 43px;
    padding-right: 12px;
    padding-left: 17px;
    margin-bottom: 24px;
    display: flex;
    align-items: center;

    span {
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}