.icon {
  width: 100%;
  height: 100%;

  &_black {
    fill: $black;
  }

  &_white {
    fill: $white;
  }

  &_primary {
    fill: var(--main-500);
  }

  &_secondary {
    fill: var(--button-primary);
  }

  &_grey {
    fill: $grey;
  }

  &_darkgrey {
    fill: $darkgrey;
  }
  
  &_lightgrey {
    fill: $lightgrey;
  }

  &_red {
    fill: var(--color-red);
  }
}