.banner {
  display: flex;
  overflow: hidden;
  border-radius: 10px;
  width: 100%;
  min-height: 180px;

  @include breakpoint(xl) {
    display: none;
  }

  &__title {
    width: 100%;
    position: relative;
    z-index: 10;
    padding: 30px 0 30px 30px;
    @include fontSize(21px);
    line-height: 28px;
    min-width: 151px;
    color: $white;
    font-weight: bold;
    background: var(--main-500);

    &:before, &:after {
      position: absolute;
      content: '';
      top: 50%;
      height: 600px;
      transform: rotate(22deg) translateY(-50%);
    }

    &:before {
      right: 57px;
      width: 15px;
      background: #0E7BCF;
      box-shadow: 0px -26px 44px rgba(245, 168, 2, 0.1);
    }

    &:after {
      right: 73px;
      width: 30px;
      background: linear-gradient(259.25deg, #228EF2 28.13%, #1B76CB 62.65%);
      box-shadow: 0px -26px 44px rgba(245, 168, 2, 0.1);
    }
  }

  &__decor {
    position: absolute;
    content: '';
    top: 50%;
    width: 60px;
    height: 600px;
    transform: rotate(22deg) translateY(-50%);
    z-index: -1;
    right: 105px;
    background: var(--main-500);
  }

  &__img {
    position: relative;
    min-width: 290px;

    @include breakpoint(md) {
      min-width: auto
    }
    
    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}