.tool {
  position: fixed;
  right: -2px;
  top: 50%;
  transform: translateX(100%);
  display: flex;
  flex-direction: column;
  background: var(--main-500);
  padding: 20px;
  z-index: 120;
  transition: 0.2s;
  border-radius: 10px 0 0 10px;

  &__button {
    position: absolute;
    content: '';
    width: 20px;
    height: 20px;
    background: url(../img/arrow.svg) no-repeat center/ 100%;
    top: 50%;
    left: -17px;
    padding: 10px;
    transform: translateY(-50%) rotate(90deg);
    cursor: pointer;
  }

  &.active {
    transform: translateX(0%);
  }

  button {
    padding: 10px;
    background-color: $white;
    font-weight: 500;
    border-radius: 10px;
    transition: .2s;
    text-align: center;

    &:not(:last-of-type) {
      margin-bottom: 10px;
    }
    
    &:active {
      transform: translate(1px, 1px);
    }
  }
}