.callback {
  display: flex;
  overflow: hidden;
  border-radius: 10px;
  color: $white;

  &_main {
    position: relative;
    display: flex;
    flex-shrink: 0;
    width: 750px;
    background: var(--main-500);
  }

  &_product-page {
    width: 100%;
    max-height: 183px;
    margin-top: 40px;
    margin-bottom: 40px;
  }

  &__wrapper {
    position: relative;
    z-index: 10;
    padding: 20px 30px;
  }

  &__bg {
    position: absolute;
    z-index: 5;
    top: 0;
    right: 0;
    bottom: 0;

    img {
      height: 100%;
    }
  }

  &__title {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: relative;
    z-index: 10;
    padding: 25px 20px 25px 30px;
    background: var(--main-500);
  }

  &__decor {
    position: absolute;
    content: '';
    top: 50%;
    width: 150px;
    height: 600px;
    transform: rotate(22deg) translateY(-50%);
    z-index: -1;
    right: 60px;
    background: var(--main-500);
  }

  &__form {
    position: relative;
    display: flex;
    @include fontSize(17px);
    border-radius: 75px;
    color: $black;
    margin-top: 25px;
    overflow: hidden;
    z-index: 1;
    max-width: 455px;
    width: 100%;

    &:before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: -1;
      filter: brightness(0.8);
      background: var(--main-500);
    }
  }

  &__msg {
    width: 100%;
    max-width: 500px;
    height: 110px;
    margin: 20px 0 14px;
    padding: 10px 18px;
    resize: none;
    outline: none;
    border: 0;
    border-radius: 10px;
    background: var(--main-100);
    @include fontSize(15px);
    line-height: 23px;
    color: $black;

    &::placeholder {
      color: $black;
    }
  }

  &__form-field {
    display: flex;
    align-items: center;
    width: 410px;
    height: 44px;
    background: var(--main-100);
    border-radius: 75px;
  }

  &__tel {
    padding: 6px 15px;
    font-size: 15px;
    background: transparent;
    color: #fff;

    &::placeholder {
      color: #fff;
    }

    .callback__form-field & {
      color: $black;

      &::placeholder {
        color: $black;
      }
    }
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 150px;
    height: 40px;
    margin: 0 2px;
    font-weight: 500;
    @include fontSize(12px);
    color: var(--main-100);
    background: var(--color-red);
    border-radius: 75px;

    &:hover {
      background: var(--color-red-light);
    }

    &:focus {
      background: var(--color-red-dark);
    }
  }

  &__submit {
    min-width: 209px;
    margin: 6px;
    border-radius: 50px;
    padding: 11px 12px 11px 14px;
    font-weight: 500;
    display: flex;
    align-items: center;
    background: #fff;
    color: var(--main-500);
    font-size: 12px;
    line-height: 18px;

    .icon {
      width: 15px;
      height: 15px;
      margin-right: 13px;
      transform: scale(-1, 1);
    }
  }

  &__name {
    @include fontSize(22px);
    font-weight: bold;
    width: calc(100% + 130px);

    @include breakpoint(md) {
      width: 100%
    }
  }

  &__desc {
    @include fontSize(15px);
    margin-top: 10px;
  }

  &__img {
    position: relative;
    min-width: 333px;

    @include breakpoint(md) {
      display: none;
    }
    
    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}