.promo {
  display: flex;
  flex-wrap: wrap;

  &__item {
    display: flex;
    align-items: center;
    width: 50%;
    height: 50px;
    margin-bottom: 35px;

    &:nth-child(even) {
      padding-left: 20px;
    }
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 50px;
    margin-right: 15px;
  }

  &__text {
    font-weight: bold;
    @include fontSize(12px);
    line-height: 20px;
    color: var(--main-900);
  }
}
