.product-card {
  display: flex;
  align-items: flex-start;
  width: 100%;
  margin-top: 20px;

  @include breakpoint(xl) {
    flex-direction: column;
    margin-top: 294px;
  }

  &__wrap {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;

    @include breakpoint(xl) {
      max-width: 100%;
      width: 100%;
      flex: 1 1 100%;
    }

    &:first-of-type {
      max-width: 520px;
      width: 100%;
      padding-right: 30px;

      @include breakpoint(xl) {
        max-width: 100%;
        width: 100%;
        flex: 1 1 100%;
        padding-right: 0;
        border-bottom: 1px solid #E1E3E6;
        margin-bottom: 20px;
      }
    }
  }

  &__inner {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
  }

  &__buttons {
    display: flex;
    flex-wrap: wrap;
    margin: 8px -8px -8px -8px;

    @include breakpoint(xl) {
      margin-top: 0;
      justify-content: center;
    }

    & > * {
      margin: 8px;

      &:not(:first-of-type) {
        flex: 1 1;

        @include breakpoint(xl) {
          flex-basis: 150px;
          flex-grow: 0;
          margin-top: 15px;
        }
      }

      @include breakpoint(xl) {
        margin: 0px 8px 14px;
      }
    }
  }

  &__blockquote {
    background-color: #F3FAFF;
    border-radius: 5px;
    padding: 15px;
    margin-bottom: 23px;
    font-size: 14px;
    line-height: 20px;
  }

  &__code {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 6.5px 8px;
    border-radius: 5px;
    color: $white;
    margin-right: 18px;
    background: var(--main-500);

    @include breakpoint(xl) {
      font-size: 13px;
    }
  }

  &__price {
    @include fontSize(24px);
    font-weight: 500;
    margin-right: 40px;
  }

  &__slider {
    margin-bottom: 40px;

    @include breakpoint(xl) {
      margin-bottom: 40px;
      position: absolute;
      width: 600px;
      top: 87px;
      left: 50%;
      transform: translateX(-50%);
    }

    @include breakpoint(md) {
      left: 0;
      transform: none;
      width: 100%;
    }
  }

  &__info {
    margin-bottom: 30px;
    font-size: 14px;
    line-height: 18px;

    @include breakpoint(xl) {
      font-size: 13px;
    }

    &:last-of-type {
      @include breakpoint(xl) {
        margin-bottom: 20px;
      }
    }

    p {
      margin: 0;

      &:not(:last-of-type) {
        margin: 0 0 6px 0;
      }

      &.product-card__info-name {
        margin-bottom: 4px;
        color: $darkgrey;
      }
    }
  }

  &__callback {
    display: flex;
    align-items: center;
    border-top: 1px solid #E1E3E6;
    padding-top: 17px;
    margin-top: 40px;

    @include breakpoint(xl) {
      display: none;
    }

    .icon {
      width: 45px;
      height: 52px;
      margin-right: 16px;
    }

    &-wrap {
      display: flex;
      flex-direction: column;
      @include fontSize(15px);
    }
    
    &-tel {
      @include fontSize(24px);
      font-weight: bold;
      margin-top: 4px;
    }
  }
}



  

  