.header {
  $self: &;
  z-index: 104;
  position: absolute;
  top: 0;
  width: 100%;
  background: #fff;

  @include breakpoint(xl) {
    position: static;
  }

  &.sticky {
    position: fixed;
    width: 100%;

    @include breakpoint(xl) {
      box-shadow: none;
    }
  }

  &__scroll {
    display: none;

    .header__sub {
      display: flex;
      align-items: center;
      padding-top: 0;
      padding-bottom: 0;
      height: 44px;
    }

    .header__contacts {
      margin-left: 0;
    }

    .header__tel {
      font-size: 12px;
      line-height: 18px;
    }

    .header__messengers {
      .icon {
        width: 19px;
        height: 16px;
      }
    }

    .header__cart {
      margin-left: auto;
    }

    .header__buttons {
      margin-left: 20px;
      padding-left: 20px;
      border-left: 1px solid $lightgrey;

      button {
        color: var(--button-primary);
        font-size: 12px;
        font-weight: 400;
        text-decoration: underline;

        &:not(:last-of-type) {
          margin-right: 20px;
        }
  
        &:hover {
          color: var(--button-primary-light);
        }
      }
    }
  }

  &__inner {
    position: relative;
  }  

  &__top {
    background: $white;
    @include breakpoint(xl) {
      position: fixed;
      top: 0;
      width: 100%;
      z-index: 104;
    }

    .container {
      @include breakpoint(xl) {
        max-width: 100%;
      }
    }
  }

  &__nav {
    margin: 0;
    padding-left: 0;
    display: flex;
    align-items: center;
    list-style: none;
    z-index: 110;

    @include breakpoint(xl) {
      display: none;
    }

    > li {
      &:not(:first-of-type) {
        margin-left: 27px;
      }

      > a {
        color: $black;
        text-decoration: none;
        font-weight: 500;
        padding: 20px 0;
        position: relative;
        display: flex;
        align-items: center;
      }

      &:hover > a {
        color: var(--button-primary);
        fill: var(--button-primary);
        &:before {
          position: absolute;
          content: '';
          bottom: -1px;
          left: 0;
          width: 100%;
          height: 3px;
          background-color: var(--button-primary);
        }
      }
    }
  }

  &__logo {
    z-index: 100;
    display: block;
    max-height: 65px;

    img {
      max-height: 65px;
    }

    @include breakpoint(xl) {
      margin: auto;
      img {
        width: 180px;
      }
    }
  }

  &__sub {
    margin: 0;
    padding-bottom: 15px;
    padding-top: 15px;
  }

  &__tel {
    @include fontSize(17px);

    &_border {
      padding-left: 18px;
      border-left: 1px solid $lightgrey;
    }

    @include breakpoint(xl) {
      margin-bottom: 10px;
    }
  }

  &__more {
    position: relative;

    > a:before {
      display: none;
    }

    ul {
      z-index: 99;
      left: 50%;
      top: 80%;
      transform: translateX(-50%);
      visibility: hidden;
      opacity: 0;
      transition: .1s;
      list-style: none;
      position: absolute;
      padding: 0;
      background: $white;
      border: 1px solid $lightgrey;
      box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1);
      border-radius: 5px;

      li {
        &:not(:last-of-type) {
          border-bottom: 1px solid $lightgrey;
        }

        a {
          width: 100%;
          font-weight: 500;
          padding: 16px;
          white-space: nowrap;
        }
      }
    }

    .icon {
      width: 17.33px;
      height: 4px;
    }

    &:hover {
      ul {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  &__buttons {
    display: flex;
    margin-left: 26px;

    @include breakpoint(xl) {
      flex-direction: column;
      margin-left: 0;
      width: 100%;
      position: relative;
      padding-bottom: 24px;
    }

    &::before {
      @include breakpoint(xl) {
        content: '';
        position: absolute;
        width: calc(100% + 48px);
        height: 1px;
        background-color: #E1E3E6;
        left: -24px;
        bottom: 0;
      }
    }
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #F0F3F8;
    border-radius: 5px;
    padding: 12px;
    line-height: 16px;
    color: $black;
    text-decoration: none;
    width: 170px;
    font-weight: 400;
    @include fontSize(12px);
    min-height: 60px;

    @include breakpoint(xl) {
      width: 100%;
      margin: 5px 0;
    }

    &:hover {
      color: var(--button-primary-light);
      
      svg {
        fill: var(--button-primary-light);
      }
    }

    &:not(:first-of-type) {
      margin-left: 10px;
      @include breakpoint(xl) {
        margin-left: 0;
      }
    }

    .icon {
      width: 35px;
      height: 35px;
      margin-left: 9px;
      @include breakpoint(xl) {
        width: 24px;
        height: 24px;
      }
    }
  }

  &__contacts {
    display: flex;
    align-items: center;
    margin-left: auto;

    @include breakpoint(xl) {
      display: none;
    }
  }

  &__messengers {
    display: flex;
    align-items: center;
    padding: 0px 18px;

    @include breakpoint(xl) {
      margin: 10px 0 17px;
    }

    a {
      width: 17px;
      height: 17px;

      &:not(:first-of-type) {
        margin-left: 16px;
      }

      @include breakpoint(xl) {
        width: 30px;
        height: 30px;

        &:not(:first-of-type) {
          margin-left: 36px;
        }
      }
    }
  }

  &__social {
    z-index: 100;
    display: flex;
    align-items: center;
    padding: 0px 18px;

    @include breakpoint(xl) {
      display: none;
    }

    a {
      width: 18px;
      height: 17px;
      &:not(:first-of-type) {
        margin-left: 30px;
      }
    }
  }

  &__location {
    cursor: pointer;
    z-index: 100;
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 18px;
    border-left: 1px solid $lightgrey;

    @include breakpoint(xl) {
      display: none;
    }

    &:hover {
      color: var(--button-primary-light);

      .icon {
        fill: var(--button-primary-light);
      } 
    }
    
    .icon {
      width: 16px;
      height: 16px;
      margin-right: 8px;
    }
  }

  &__hamburger {
    position: relative;
    display: none;
    height: 15px;
    width: 18px;
    align-self: center;

    span {
      top: 6px; 
    }

    span:before {
      top: -6px;
    }

    span:after {
      bottom: -6px;
    }

    span, span:before, span:after {
      content: '';
      border-radius: 5px;
      position: absolute;
      height: 3px;
      background-color: var(--main-500);
      width: 100%;
    }

    @include breakpoint(xl) {
      display: block;
    }
  }

  &__contact {
    display: none;
    align-self: center;

    @include breakpoint(xl) {
      display: block;
      z-index: 100;
    }

    .icon {
      width: 18px;
      height: 20px;
    }
  }

  &__wrap {
    display: flex;
    margin-left: auto;
    position: relative;

    @include breakpoint(xl) {
      flex: 1 1 100%;
      margin-left: 0;
      justify-content: space-between;
      height: 44px;
    }
  }

  &__mobile-menu {
    display: none;
    visibility: hidden;
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    margin: 0;
    z-index: 110;

    @include breakpoint(xl) {
      display: block;
    }

    &-content {
      width: calc(100% - 30px);
      background: $white;
      transform: translateX(-100%);
      transition: .2s;
      height: 100vh;
      overflow-x: hidden;
      overflow-y: auto;
      padding-bottom: 30px;
    }

    &-overlay {
      width: 100%;
      height: 100%;
      position: fixed;
      top: 0;
      z-index: -1;
      opacity: 0;
      transition: .2s;
      backdrop-filter: blur(2px);
      background-color: #141414;
    }

    &-wrap {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 24px 16px;
    }

    ul {
      list-style: none;
      padding: 0 0 12px 0;
      margin: 0;
      border-bottom: 1px solid $lightgrey;

      li {
        a {
          padding: 12px 16px;
          width: 100%;
          font-weight: 500;
        }
      }
    }

    &.active {
      visibility: visible;

      #{$self}__mobile-menu-content {
        transform: translateX(0%);
      }

      #{$self}__mobile-menu-overlay {
        opacity: 0.9;
      }
    }

    .header__social {
      display: flex;
      padding: 27px 0;

      a {
        @include breakpoint(xl) {
          width: 30px;
          height: 30px;
  
          &:not(:first-of-type) {
            margin-left: 36px;
          }
        }
      }
    }
  }
}

@media (max-width: 1200px){
  .header__inner:nth-child(2){
    padding-top: 40px;
  }
  .main{
    padding-top: 32px;
  }
}