.search {
  $self: &;
  padding: 12px 0;
  background-color: var(--main-500);
  min-width: 1230px;
  position: absolute;
  top: 72px;
  width: 100%;

  @include breakpoint(xl) {
    min-width: auto;
    padding: 16px 0;
    margin-bottom: 20px;
  }

  &.active {
    #{$self}__close {
      visibility: visible;
      opacity: 1;
      display: flex;
    }

    #{$self}__clean {
      display: none;
    }
  }

  &.sticky {
    position: fixed;
    width: 100%;
    z-index: 100;
  }

  &-popup {
    z-index: 99;
    visibility: hidden;
    opacity: 0;
    transition: .3s;
    cursor: default;
    position: absolute;
    width: 420px;
    background: #fff;
    top: 128%;
    border: 1px solid #E1E3E6;
    border-radius: 5px;
    box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1);
    left: -13%;
    display: flex;
    flex-direction: column;
    padding: 17px 20px 24px;
    text-align: center;
    color: $black;

    &::before {
      content: '';
      position: absolute;
      top: -6px;
      left: 50%;
      width: 12px;
      height: 12px;
      background-color: $white;
      transform: translateX(-50%) rotate(45deg);
      border: 1px solid #E1E3E6; 
    }

    &::after {
      content: '';
      position: absolute;
      top: 0px;
      left: 50%;
      width: 20px;
      height: 12px;
      background-color: $white;
      transform: translateX(-50%);
    }

    p {
      margin: 0 0 16px;
      line-height: 18px;
    }

    .icon-circle {
      width: 68px;
      height: 68px;
      margin-bottom: 16px;
      position: relative;

      .icon {
        width: 35px;
        height: 27px;
        margin: 0;
      }
    }

    &.active {
      visibility: visible;
      opacity: 1;
    }

    @include breakpoint(xl) {
      right: auto;
      left: 50%;
      transform: translateX(-50%);
      width: calc(100vw - 30px);

      &::before {
        right: auto;
        left: 50%;
        transform: translateX(-50%) rotate(45deg);
      }

      &::after {
        right: auto;
        left: 50%;
        transform: translateX(-50%);
      }
    }

    &__title {
      font-size: 16px;
      font-weight: 500;
      margin-bottom: 16px;
    }

    .link {
      font-size: 12px;
    }
  }

  &__input {
    position: relative;
    display: flex;
    align-items: stretch;
    border-radius: 5px;
    background-color: $white;
    padding: 6px 5px 5px 12px;
    color: $darkgrey;
    max-width: 265px;
    min-width: 200px;
    width: 100%;
    z-index: 100;

    @include breakpoint(xl) {
      min-width: 100%;
    }

    &.active {
      border-radius: 0;
      border-bottom: 1px solid var(--button-primary);

      #{$self}__icon {
        margin-right: -6px;
      }
    }

    input {
      text-overflow: ellipsis;
      overflow: hidden;
      padding-right: 20px;
      @include fontSize(12px);
    }
  }

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 31px;
    height: 31px;
    border-radius: 5px;
    z-index: 2;
    background-color: var(--color-red);

    @include breakpoint(xl) {
      min-width: 36px;
      height: 36px;
    }

    &:hover {
      background-color: var(--color-red-light);
    }

    .icon {
      width: 14px;
      height: 14px;
    }
  }

  &__close {
    visibility: hidden;
    align-items: center;
    cursor: pointer;
    z-index: 100;
    position: relative;
    right: -20px;
    margin-right: -70px;
    font-size: 13px;
    line-height: 20px;
    color: #909499;
    opacity: 0;
    will-change: opacity;
    transition: opacity 0.3s;

    .icon {
      min-width: 14px;
      width: 14px;
      height: 14px;
      margin-right: 8px;
    }
  }
  &__clean {
    transform: translateY(-50px);
    opacity: 0;
    position: relative;
    height: 14px;
    z-index: 100;
    top: 15px;
    right: 40px;
    cursor: pointer;

    @include breakpoint(xl) {
      display: block;
      position: absolute;
      opacity: 0;
    }

    .icon {
      width: 14px;
      height: 14px;
    }

    &.active {
      transform: translateY(0px);
      opacity: 1;
    }
  }

  &__name {
    align-self: center;
    padding-left: 18px;
    margin-left: 18px;
    border-left: 1px solid $white;
    opacity: 0.8;
    color: $white;
    margin-right: 19px;
    white-space: nowrap;
    font-size: 11px;
    padding-top: 2.5px;
    padding-bottom: 2.5px;

    @include breakpoint(xl) {
      margin: 35px 0 8px 0;
      padding: 0;
      border: none;
      font-size: 13px;
      font-weight: 500;
      opacity: 1;
    }
  }

  &__multiselect {
    display: flex;
    align-items: center;
    border-radius: 5px;
    background-color: $white;
    flex: 1 1 100%;
    position: relative;
    @include breakpoint(xl) {
      background: none;
      flex-direction: column;
    }

    .multiselect {
      border: none;
      
      &:not(:first-of-type) {
        border-left: 1px solid $lightgrey;
        border-radius: 0;

        @include breakpoint(xl) {
          border-radius: 5px;
          max-width: 100%;
          border: none;
        }
      }

      &:last-of-type {
        border-right: 1px solid $lightgrey;

        @include breakpoint(xl) {
          border-radius: 5px;
          max-width: 100%;
          border: none;
        }
      }

      @include breakpoint(xl) {
        margin-bottom: 8px;
      }
    }
  }

  &__submit {
    padding: 0;
    margin: 0 5px;
    min-width: 133px;
    max-width: 133px;
    height: 30px;
    font-size: 13px;

    @include breakpoint(xl) {
      width: 100%;
      max-width: 100%;
      padding: 16px 0;
      min-height: 48px;
    }
  }

  &__contact {
    width: 100%;
    max-width: 520px;
    position: absolute;
    width: 100%;
    max-width: 520px;
    top: -77px;
    right: 15px;

    @include breakpoint(xl) {
      display: none;
    }

    &-wrap {
      display: flex;
      align-items: center;
      margin-top: 15px;
      font-size: 15px;
      line-height: 18px;
      .icon {
        width: 37px;
        height: 42px;
        margin-right: 16px;
      }
    }

    &-tel {
      @include fontSize(24px);
      font-weight: bold;

      &:first-of-type {
        border-right: 1px solid #C4C8CC;
        padding-right: 16px;
        margin-right: 16px;
      }
    }
  }

  &__overlay {
    display: none;
    background-color: $white;
    position: fixed;
    top: 0;
    left: 0;
    height: 680px;
    width: 100%;
    z-index: -1;
    opacity: 0;

    @include breakpoint(xl) {
      height: 100vh;
    }
    
    .overlay {
      position: fixed;
      top: 680px;
      right: 0;
      left: 0;
      bottom: 0;
      backdrop-filter: blur(2px);
      background-color: $black;
      z-index: 150;
      height: 100vh;
      visibility: hidden;
      @include breakpoint(xl) {
        display: none;
      }
    }

    &.active {
      display: block;
      z-index: 99;
      opacity: 1;

      .overlay {
        visibility: visible;
        opacity: 0.9;
      }

      .search__result {
        opacity: 1;
      }
    }
  }
  &__result {
    opacity: 0;
    position: absolute;
    bottom: 20px;
    transform: translateX(-50%);
    left: 50%;
    height: 60%;
    width: 100%;
    max-width: 1230px;
    padding: 0 15px;
    
    @include breakpoint(xl) {
      height: 50%;
      max-width: 630px;
    }

    @include breakpoint(lg) {
      padding: 0 15px;
    }

    &-wrap {
      overflow: auto;
      height: 100%;
      max-width: 42.5%;
      width: 100%;

      @include breakpoint(xl) {
        max-width: 100%;
      }
    }

    &-title {
      font-weight: bold;
      margin-bottom: 8px;
    }

    ul {
      list-style: none;
      padding: 0;
      margin: 0;
      padding-bottom: 20px;

      &:last-of-type {
        padding-bottom: 70px;
      }

      li {
        &:not(:last-of-type) {
          margin-bottom: 8px;
        }
      }
    }
  }

  &_mobile {
    overflow-x: hidden;
    margin-bottom: 0;
    padding: 8px 0;
    position: relative;
    top: 0;
    z-index: 103;

    .search__clean {
      display: none;
      opacity: 0;
    }

    &.active {
      top: 0;
      position: fixed;
      height: 100vh;
      background-color: #fff;
      z-index: 104;

      .search__clean {
        display: block;
        opacity: 1;
        z-index: 105;
        left: 5px;
        top: 12px;
        position: absolute;
        padding: 10px;
        -webkit-box-sizing: content-box;
        box-sizing: content-box;
        transform: none;
        right: auto;
      }
    }

    .row {
      flex-wrap: nowrap;
      align-items: center;
    }

    .search__input {
      min-width: 175px;
      padding: 3px 3px 3px 12px;
      max-width: 100%;

      &.active {
        min-width: 100%;
        z-index: 104;
        padding: 3px 3px 3px 24px;
      }
    }

    .search__result {
      height: calc(100% - 90px);
    }
    

    .search__name {
      margin: 0 0 0 16px;
      text-decoration: underline;
      
      &:hover {
        color: #fff;
      }
    }

    .search__overlay {
      z-index: 103;
      visibility: hidden;
  
      &.active {
        visibility: visible;
        overflow: hidden;
      }
    }
  }

 &_mobile-filter {
  overflow-x: hidden;
  margin-bottom: 0;
  padding: 8px 0;
  position: relative;
  top: 0;
  z-index: 103;

  .search__clean {
    display: none;
    opacity: 0;
  }

  @include breakpoint(xl) {
    padding: 16px 0;
  }

  &.active {
    top: 0;
    position: fixed;
    height: 100%;
    overflow: hidden;
    background-color: #fff;
    z-index: 104;

    .search__clean {
      display: block;
      opacity: 1;
      z-index: 105;
      transform: translateX(-10px);
      top: 20px;
      position: absolute;
      padding: 10px;
      -webkit-box-sizing: content-box;
      box-sizing: content-box;
      right: auto;
    }
  }

  .search__input {
    padding: 3px 3px 3px 12px;

    &.active {
      min-width: 100%;
      z-index: 104;
      padding: 3px 3px 3px 24px;
    }
  }

  .search__result {
    height: calc(100% - 90px);
  }

  .search__overlay {
    z-index: 103;
    visibility: hidden;

    &.active {
      visibility: visible;
    }
  }
 }
}