html {
  // Blue
  --main-900: #0562A9;
  --main-500: #0E7BCF;
  --main-100: #F3FAFF;

  // Button
  --button-primary-dark: #015596;
  --button-primary: #015FA8;
  --button-primary-light: #026CBF;
  --button-secondary-dark: #015596;
  --button-secondary: #50A7BA;
  --button-secondary-light: #93C4D3;

  // Red
  --color-red-dark: #D6042A;
  --color-red: #E10F35;
  --color-red-light: #E93354;

  &.color-red {
    // Color
    --main-900: #AD0606;
    --main-500: #C52727;
    --main-100: #FFECEC;

    // Button
    --button-primary-dark: #ECAB03;
    --button-primary: #EC1212;
    --button-primary-light: #FFC634;
    --button-secondary-dark: #73B0C4;
    --button-secondary: #FFB017;
    --button-secondary-light: #A1CFDE;
  }

  &.color-violet {
    // Color
    --main-900: #6766C9;
    --main-500: #4F4EA1;
    --main-100: #F3F3FF;

    // Button
    --button-primary-dark: #ECAB03;
    --button-primary: #E46762;
    --button-primary-light: #E93354;
    --button-secondary-dark: #73B0C4;
    --button-secondary: #8885C0;
    --button-secondary-light: #A1CFDE;
  }

  &.color-blue-dark {
    // Color
    --main-900: #06419A;
    --main-500: #195BBD;
    --main-100: #F0F4FA;

    // Button
    --button-primary-dark: #D6042A;
    --button-primary: #E10F35;
    --button-primary-light: #E93354;
    --button-secondary-dark: #73B0C4;
    --button-secondary: #7890B4;
    --button-secondary-light: #A1CFDE;
  }

  &.color-green {
    // Color
    --main-900: #0D7A3F;
    --main-500: #199351;
    --main-100: #EAF7F0;

    // Button
    --button-primary-dark: #ECAB03;
    --button-primary: #FFA800;
    --button-primary-light: #FFC634;
    --button-secondary-dark: #73B0C4;
    --button-secondary: #7CA6BD;
    --button-secondary-light: #A1CFDE;
  }
}