@import "../assets/fonts/Roboto/stylesheet.scss";

// COLOR
$black: #141414;
$grey: #C4C8CC;
$darkgrey: #909499;
$lightgrey: #E1E3E6;
$white: #fff;

$size: 12px;

// Adaptive
$breakpoints: (
  sm: 640px,
  md: 768px,
  lg: 631px,
  xl: 1199px,
  xxl: 1440px
);

@mixin breakpoint($value, $keyword: null) {
  @media screen and (max-device-width: map-get($breakpoints, $value)) {
    @content
  }
}

@function calculateRem($val) {
  $remSize: calc($val / $size);
  @return #{$remSize}rem;
}

@mixin fontSize($val) {
  font-size: calculateRem($val);
}