.product-list {
  $self: &;
  border-top: 1px solid $lightgrey;
  width: 100%;

  @include breakpoint(xl) {
    width: 100%;
  }

  @include breakpoint(lg) {
    margin: 0 -15px;
    width: calc(100% + 30px);
  }

  &__item {
    position: relative;
    display: flex;
    border-bottom: 1px solid $lightgrey;
    padding: 24px 8px;

    @include breakpoint(xl) {
      flex-wrap: wrap;
      padding: 20px 15px;
      flex: 1 1 auto;
    }

    &:nth-child(odd) {
      &:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: -1;
        background: var(--main-100);
      }
    }

    &_cart {
      #{$self}__value {
        margin-left: auto;
        padding-left: 10px;
        font-size: 24px;

        @include breakpoint(xl) {
          margin-left: auto;
        }
      }

      #{$self}__more {
        justify-content: center;
      }

      #{$self}__inner {
        margin-bottom: 0;
      }
    }
  }
  &__preview {
    max-width: 140px;
    width: 100%;
    margin-right: 16px;

    @include breakpoint(xl) {
      order: 2;
      flex: 0 1 80px;
      margin-right: 0;
    }

    img {
      width: 100%;
    }
  }
  &__info {
    flex: 1 1 530px;
    padding-right: 15px;

    @include breakpoint(xl) {
      order: 1;
      flex: 1 1 200px;
      padding-right: 10px;
    }
  }

  &__title {
    @include fontSize(20px);
    font-weight: 500;
    margin-bottom: 12px;
    line-height: 23px;

    @include breakpoint(xl) {
      @include fontSize(16px);
    }
  }

  &__desc {
    line-height: 20px;

    @include breakpoint(xl) {
      font-size: 13px;
    }
  }

  &__value {
    @include fontSize(23px);
    font-weight: 500;
    white-space: nowrap;
  }

  &__code {
    margin-top: 4px;
    line-height: 20px;
  }

  &__more {
    display: flex;
    flex-direction: column;
    flex: 1 0 250px;
    border-left: 1px solid $lightgrey;
    padding: 16px 0 16px 14px;    
    margin: -16px 0;

    @include breakpoint(xl) {
      order: 3;
      border: none;
      padding: 20px 0 0 0;
      flex: 1 1 100%;
      margin: 0;
      
    }
  }

  &__button {
    padding: 11.5px 18px;
    font-size: 13px;
    font-weight: 400;

    .icon {
      height: 25px;
      width: 25px;
      margin-right: 8px;
    }
  }

  &__amount {
    margin-left: auto;
  }

  &__inner {
    display: flex;
    align-items: center;
    margin-bottom: 12px;
  }

  &__buttons {
    display: flex;
    margin-top: 10px;

    .button {
      @include fontSize(13px);
      padding: 10px 0;
      font-weight: 400;

      &:first-of-type {
        min-width: 140px;
      }

      .icon {
        height: 18px;
        width: 18px;
        margin-right: 5px;
        transform: scale(-1, 1);
      }

      span{
        color: #000;
      }
    }
  }

  &__remove {
    background: transparent;
    padding: 0;
    width: auto;
    margin-left: 20px;

    @include breakpoint(xl) {
      margin-left: 10px;
    }

    .icon {
      width: 14px;
      height: 18px;
    }

    &:hover {
      background: none;
    }
  }

  &__load {
    position: relative;
    background: transparent;
    @include fontSize(13px);
    font-weight: 600;
    color: var(--main-500);
    
    @include breakpoint(xl) {
      margin: 0 -15px;
      width: calc(100% + 30px);
    }

    &:hover {
      background: inherit;
      color: var(--button-primary);
    }

    &:before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: -1;
      background: var(--main-100);
    }
  }
}