.multiselect {
  $self: &;
  width: 100%;
  height: 100%;
  position: relative;
  border: 1px solid $lightgrey;
  border-radius: 5px;
  overflow: hidden;

  &:first-of-type {
    border-left: 0px solid $lightgrey;
  }

  &:before {
    pointer-events: none;
    content: '';
    position: absolute;
    top: 50%;
    right: 12px;
    width: 9px;
    height: 6px;
    background: url(../img/arrow.png);
    transform: translateY(-50%);
    @include breakpoint(xl) {
      right: 12px;
    }
  }

  &__close {
    display: none;
    cursor: pointer;
    position: absolute;
    top: 50%;
    right: 12px;
    transform: translateY(-50%);
    min-width: 14px;
    width: 14px;
    height: 14px;

    @include breakpoint(xl) {
      padding: 0px 12px;
      right: 0;
      box-sizing: content-box;
      height: 42px;
      max-height: 42px;
    }
  }

  &.active {
    &::before {
      display: none;
    }

    #{$self}__close {
      display: block;
    }
  }

  select {
    cursor: pointer;
    width: 100%;
    height: 100%;
    font-size: 13px;
    line-height: 20px;
    padding-left: 18px;
    color: #141414;
    border: none;
    height: 42px;
    -webkit-appearance: none;
    outline: none;
    background-color: #fff;

    @include breakpoint(xl) {
      padding-left: 12px;
      height: 42px;
    }

    &::-ms-expand {
      display: none;
    }

    &:focus {
      outline: none;
    }
  }

  &.disabled {
    pointer-events: none;
    
    select {
      background-color: #F0F3F8;
      color: #909499;
    }
  }

  &_select {
    margin-bottom: 16px;
  }
}