.header__cart {
  $self: &;
  position: relative;
  z-index: 101;
  cursor: pointer;
  color: $darkgrey;
  font-weight: 500;
  display: flex;
  align-items: center;
  padding: 0 20px;
  border-left: 1px solid $lightgrey;
  border-right: 1px solid $lightgrey;
  align-self: stretch;

  .icon {
    width: 16px;
    height: 16px;
    margin-right: 8px;
  }

  &.active {
    background-color: var(--button-primary);
    color: $white;

    .icon {
      fill: #fff;
    }
  }

  /*:not(.active) {
    height: 72px;
  }*/

  @include breakpoint(xl) {
    border: none;
    font-size: 13px;

    a {
      background-color: $white;
      color: $darkgrey;
      border: none;
      padding: 11px 0;
      .icon {
        fill: $darkgrey
      }
    }

    &.active a {
      background-color: $white;
      color: var(--button-primary);
    }
  }

  &-popup {
    z-index: 99;
    visibility: hidden;
    opacity: 0;
    transition: .3s;
    cursor: default;
    position: absolute;
    width: 420px;
    background: #fff;
    top: 130%;
    border: 1px solid #E1E3E6;
    border-radius: 5px;
    box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1);
    right: 0;
    display: flex;
    flex-direction: column;
    padding: 17px 20px 24px;
    text-align: center;
    color: $black;

    &::before {
      content: '';
      position: absolute;
      top: -6px;
      right: 60px;
      width: 12px;
      height: 12px;
      background-color: $white;
      transform: rotate(45deg);
      border: 1px solid #E1E3E6;
    }

    &::after {
      content: '';
      position: absolute;
      top: 0px;
      right: 57px;
      width: 20px;
      height: 12px;
      background-color: $white;
    }

    p {
      margin: 0 0 16px;
      line-height: 18px;
    }

    .icon-circle {
      width: 68px;
      height: 68px;
      margin-bottom: 16px;
      position: relative;

      .icon-circle__count {
        position: absolute;
        top: 0;
        right: 0;
        width: 17px;
        height: 17px;
        color: $white;
        @include fontSize(10px);
        font-weight: bold;
        border-radius: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: var(--button-primary);
      }

      .icon {
        width: 30px;
        height: 32px;
        margin: 0;
      }
    }

    &.active {
      visibility: visible;
      opacity: 1;
    }

    @include breakpoint(xl) {
      right: auto;
      left: 50%;
      transform: translateX(-50%);
      width: calc(100vw - 30px);

      &::before {
        right: auto;
        left: 50%;
        transform: translateX(-50%) rotate(45deg);
      }

      &::after {
        right: auto;
        left: 50%;
        transform: translateX(-50%);
      }
    }

    .link {
      font-size: 12px;
    }
  }
}
