.header {
  &.header_main & {
    &__wrapper {
      display: flex;
      padding-right: 15px;
    }

    &__sub {
      &_fixed-header {
        flex-grow: 1;
        padding-bottom: 17px;
        padding-top: 17px;
        padding-right: 0;
      }
    }

    &__logo {
      max-height: 38px;
    }

    &__buttons {
      margin-left: 40px;

      &.no-margin {
        margin-left: 0;
      }
    }

    &__button {
      height: 38px;
      min-height: 0;
      color: var(--color-red);

      &:not(:first-of-type) {
        margin-left: 15px;
      }

      .icon {
        width: 32px;
        height: 32px;
      }

      &:hover {
        color: var(--color-red-light);

        .icon {
          fill: var(--color-red-light);
        }
      }
    }

    &__tel {
      &_fixed-header {
        font-weight: 500;
        @include fontSize(20px);
      }
    }

    &__messengers {
      .header__link {
        width: 26px;
        height: 26px;

        &:not(:first-of-type) {
          margin-left: 10px;
        }
      }
    }

  }
}
