.sidebar {
  $self: &;
  display: flex;
  flex: 1 0 270px;
  align-self: flex-start;
  margin-bottom: 20px;
  flex-wrap: wrap;
  z-index: 11;

  @include breakpoint(xl) {
    position: static;
    width: 100%;
  }

  @include breakpoint(lg) {
    position: static;
    margin: 0 -15px;
    width: calc(100% - 30px);
  }

  &.sticky {
    position: sticky;
  }

  &__title {
    @include fontSize(22px);
    font-weight: bold;
    margin-bottom: 20px;
    width: 100%;
  }

  &__list, &__banners {
    padding: 0;
    margin: 0 0 9px 0;
    list-style: none;
    width: 100%;
    position: relative;

    @include breakpoint(xl) {
      margin: 0;
    }
  }

  &__banners {
    margin-right: -10px;
  }

  &__banner {
    & + & {
      margin-top: 20px;
    }

    img {
      vertical-align: top;
    }

    i {
      display: block;
      background: #bf0303;
      color: #ffffff;
      font-style: normal;
      text-align: center;
      height: 45px;
      line-height: 43px;
      font-size: 18px;
      font-weight: 600;
      text-transform: uppercase;
      position: relative;
      overflow: hidden;

      &:before {
          content: '';
          display: block;
          height: 350%;
          width: 150px;
          background: linear-gradient(to right, rgba(255,255,255, 0), rgba(255,255,255, .3));
          transform: skew(-35deg, -35deg);
          position: absolute;
          z-index: 5;
          top: -50px;
          -webkit-animation: blink_s 3s 2s linear infinite;
          animation: blink_s 3s 2s linear infinite;
      }

      @-webkit-keyframes blink_s {
        0% {left: -200%;}
        100% { left: 450%;}
      }
      @keyframes blink_s {
        0% {left: -10%;}
        100% { left: 450%;}
      }

      &:hover {
        background: #a51010;
      }
    }
  }

  &__subitems {
    display: none;
    overflow: auto;
    margin-bottom: 24px;
    padding-left: 15px;

    @include breakpoint(xl) {
      padding-left: 0;
      flex-wrap: wrap;
    }

    @include breakpoint(lg) {
      padding: 0 16px;
    }
  }

  &__subitem {
    padding: 17px 12px;
    width: 100%;
    border-left: 5px solid #F0F3F8;

    @include breakpoint(xl) {
      width: 50%;
    }

    @include breakpoint(lg) {
      width: 100%;
    }

    &:not(:last-of-type) {
      margin-bottom: 8px;
    }
  }

  &__item {
    @include fontSize(15px);
    border-bottom: 1px solid $lightgrey;
    position: relative;

    &.active {
      background: var(--main-100);
    }

    &:nth-of-type(n+11) {
      display: none;
    }

    &:nth-of-type(-n+3) {
      #{$self}__sublist {
        top: 0px;
        transform: translateX(270px) translateY(0);
      }
    }

    &:nth-of-type(n+4) {
      #{$self}__sublist {
        top: 50%;
        transform: translateX(270px) translateY(-50%);
      }
    }

    &:nth-last-of-type(-n+7) {
      #{$self}__sublist {
        top: auto;
        bottom: 0px;
        transform: translateX(270px) translateY(0);
      }
    }


    &:first-of-type {
      border-top: 1px solid $lightgrey;
    }

    > a {
      padding: 15px 30px 15px 10px;
      width: 100%;
      display: inline-block;
      position: relative;
    }

    &.show {
      display: block;
    }
  }

  &__name {
    padding: 15px;
    width: 100%;
    display: inline-block;
    position: relative;
    color: var(--main-500);
    text-decoration: underline;

    @include breakpoint(xl) {
      padding: 20px 20px 20px 0;
      font-weight: 500;
      font-size: 16px;
    }

    @include breakpoint(lg) {
      padding: 15px 16px;
    }

    &:after {
      transition: .2s;
      content: '';
      position: absolute;
      right: 12px;
      top: 50%;
      transform: translateY(-50%);
      background: url(../img/arrow.svg) no-repeat center / cover;
      width: 9px;
      height: 6px;
    }

    &.active {
      color: #141414;
      text-decoration: none;
      &::after {
        transform: translateY(-50%) rotate(180deg);
      }
    }
  }

  &__all {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 10;
    width: 30px;
    height: 100%;
    cursor: pointer;

    &.active {
      &::before {
        transform: translate(-50%, -50%) rotate(-90deg);
      }
    }

    &::before {
      position: absolute;
      content: '';
      background: url(../img/arrow.png);
      width: 9px;
      height: 6px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: .1s;
    }
  }

  &__sublist {
    display: none;
    position: absolute;
    left: 0;
    top: 50%;
    width: 440px;
    max-height: 510px;
    overflow-y: auto;
    padding: 0;
    transform: translateX(270px) translateY(-50%);
    background-color: #fff;
    border: 1px solid #E1E3E6;
    box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    list-style: none;
    user-select: none;

    &.active {
      display: block;
    }

    li {
      border-bottom: 1px solid #E1E3E6;
    }

    a {
      width: 100%;
      padding: 16px 12px;
    }
  }

  &__more {
    font-weight: bold;
    text-decoration: underline;
    @include fontSize(15px);
    color: var(--button-primary);

    &.active {
      color: $darkgrey;
    }
  }
}