.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  @include fontSize(14px);
  color: $white;
  text-align: center;
  font-weight: 500;
  padding: 16px;
  border-radius: 5px;
  background-color: var(--button-primary);

  &:hover {
    background-color: var(--button-primary-light);
  }

  &:active {
    background-color: var(--button-primary-dark);
  }
  
  &_icon {
    .icon {
      width: 18px;
      height: 24px;
      margin-right: 10px;
    }
  }
  &_text-primary {
    background: transparent;
    color: var(--main-500);

    &:hover {
      background-color: transparent;
      color: var(--button-primary-light);

      svg {
        fill: var(--button-primary-light)
      }
    }
  
    &:active {
      background-color: transparent;
      color: var(--button-primary-dark);
    }
  }

  &_text-secondary {
    background: none;
    color: var(--button-primary);

    &:hover {
      background-color: $white;
      color: var(--button-primary-light);
    }
  
    &:active {
      background-color: $white;
      color: var(--button-primary-dark);
    }
  }

  &_button-secondary {
    background: var(--button-secondary);

    &:hover {
      background-color: var(--button-secondary-light);
    }
  }

  &_button-red {
    background: var(--color-red);

    &:hover {
      background-color: var(--color-red-light);
    }

    &:active {
      background-color: var(--color-red-dark);
    }
  }

  &_color-darkgray {
    color: $darkgrey;
    background: #F0F3F8;
    &:hover {
      background-color: var(--button-secondary-light);
      color: #fff;
    }
  }

  &_no-bg {
    min-width: auto;
    width: auto;
    padding: 0;
    background: transparent;
  }
}