.tab {
  width: 100%;
  margin-top: 20px;

  &__link {
    border-bottom: 1px solid #E1E3E6;
    display: flex;

    @include breakpoint(xl) {
      overflow-x: auto;
      margin: 0 -15px;
      width: calc(100% + 30px);
    }
  }

  &__name {
    cursor: pointer;
    position: relative;
    font-weight: bold;
    @include fontSize(15px);
    padding-bottom: 10px;
    white-space: nowrap;

    &:not(:first-of-type) {
      margin-left: 30px;
    }

    &:first-of-type {
      @include breakpoint(xl) {
        padding-left: 15px;
      }
    }

    &:last-of-type {
      @include breakpoint(xl) {
        padding-right: 15px;
      }
    }

    &.active {
      color: var(--button-primary);

      &:before {
        content: '';
        position: absolute;
        bottom: -2px;
        width: 100%;
        height: 2px;
        background: var(--button-primary);
        @include breakpoint(xl) {
          bottom: 0;
        }
      }
    }
  }

  &__item {
    display: none;

    &.active {
      display: block;
    }
  }
  
  &__content {
    margin-top: 24px;
  }
}