@import "./variables.scss";
@import "./color-theme";

* {
  box-sizing: border-box;
}

html {
  font-size: $size;

  &.lock {
    overflow: hidden;
    padding-right: 17px;
    height: 100vh;
    @include breakpoint(xl) {
      padding-right: 0;
    }
  }
}

br {
  @include breakpoint(xl) {
    display: none;
  }
}

body {
  color: $black;
  margin: 0;
  padding-top: 144px;
  font-family: Roboto, -apple-system, BlinkMacSystemFont, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";

  &.lock {
    overflow: hidden;
    height: 100vh;
  }

  &.desktop {
    .container {
      min-width: 1230px;
    }
  }

  &.mobile {
    .breadcrumbs {
      margin-bottom: 45px;
    }

    .breadcrumbs__mobile-button {
      top: 191px;
    }
  }

  @include breakpoint(xl) {
    position: relative;
    top: 0;
    padding-top: 44px;
  }
}

button {
  border: none;
  cursor: pointer;
  text-align: left;
  background: none;
  padding: 0;
  font-weight: 500;
  font-family: Roboto, -apple-system, BlinkMacSystemFont, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  &:focus {
    outline: none;
  }
}

a {
  color: $black;
  text-decoration: none;
  display: inline-block;

  &:hover {
    color: var(--button-primary-light);

    svg {
      fill: var(--button-primary-light);
    }
  }

  &:active {
    color: var(--button-primary-dark);

    svg {
      fill: var(--button-primary-dark);
    }
  }
}

input, textarea {
  width: 100%;
  border: none;
  padding: 0;
  font-family: Roboto, -apple-system, BlinkMacSystemFont, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  &:focus {
    outline: none;
  }
  &::-webkit-outer-spin-button, &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  &[type=number] {
    -moz-appearance: textfield;
  }
}

.container {
  max-width: 1230px;
  width: 100%;
  margin: 0 auto;

  @include breakpoint(xl) {
    max-width: 630px;
  }
}
.row {
  display: flex;
  padding: 0 15px;
  // @include breakpoint(xxl) {
  //   padding: 0 40px;
  // }
  @include breakpoint(xl) {
    flex-wrap: wrap;
  }
  @include breakpoint(lg) {
    padding: 0 15px;
  }
}

.link {
  cursor: pointer;
  @include fontSize(15px);
  text-decoration: underline;
  font-weight: bold;
  color: var(--button-primary);
  &:not(:last-of-type) {
    margin-bottom: 16px;
  }

  &:hover {
    color: var(--button-primary-light);
  }
}

.main {
  display: flex;
  margin-top: 40px;
  margin-bottom: 40px;
  @include breakpoint(xl) {
    margin-top: 0px;
    margin-bottom: 20px;
  }
}

.content {
  display: inline-flex;
  flex-wrap: wrap;
  align-items: flex-start;
  align-content: flex-start;
  width: calc(100% - 270px);
  padding-left: 40px;
  @include breakpoint(xl) {
    width: 100%;
    padding-left: 0;
  }

  &_full-width {
    width: 100%;
    padding-left: 0;
  }
}

.title {
  @include fontSize(23px);
  font-weight: 400;
  margin-bottom: 20px;
  line-height: 34px;
  width: 100%;
  @include breakpoint(xl) {
    font-weight: 500;
    @include fontSize(18px);
    margin-top: 20px;
    line-height: 26px
  }
  &_product-card {
    @include breakpoint(xl) {
      position: relative;
      top: 294px;
    }
  }

  &_modal {
    @include fontSize(20px);
    font-weight: 500;
    text-align: center;
    @include breakpoint(xl) {
      @include fontSize(18px);
    }
  }

  &_main {
    margin-bottom: 14px;
    font-weight: 700;
    @include fontSize(36px);
    line-height: 34px;
  }
}

.text {
  margin-bottom: 4px;
  @include fontSize(23px);
  font-weight: 400;
  line-height: 34px;
}

.subtitle {
  @include fontSize(14px);
  @include breakpoint(xl) {
    @include fontSize(15px);
  }
  &_modal {
    text-align: center;
  }
}

.form {
  $self: &;

  .error {
    &__text {
      color: #ED2C2C;
      line-height: 20px;
      margin-top: 4px;
    }
  }

  &__group {
    &:not(:last-of-type) {
      margin-bottom: 20px;
    }

    &.error {
      #{$self}__input {
        border-color: #ED2C2C;
        background-color: #FFF2F2;
      }
    }

    &.disabled {
      #{$self}__label {
        color: #909499;
      }

      #{$self}__input {
        pointer-events: none;
        color: #909499;
        background-color: #F0F3F8;

        &::placeholder {
          color: #C4C8CC;
        }
      }
    }
  }
  
  &__label {
    margin-bottom: 4px;
    display: block;
    text-align: left;
  }

  &__input {
    border: 1px solid $lightgrey;
    border-radius: 5px;
    padding: 15px 14px;
    width: 100%;

    &:focus {
      border-color: #C4C8CC;
    }

    &::placeholder {
      color: #909499
    }
  }

  &__textarea {
    border: 1px solid $lightgrey;
    border-radius: 5px;
    padding: 16px 14px;
    resize: none;
    width: 100%;
    min-height: 100px;
    &:focus {
      outline: none;
    }
  }

  &__info {
    display: block;
    margin-top: 4px;
    color: $darkgrey;
  }

  &__checkbox {
    user-select: none;
    display: flex;
    align-items: center;
    position: relative;
    min-height: 20px;
    text-align: left;

    label {
      padding-left: 28px;
      z-index: 1;
      cursor: pointer;
    }

    a {
      text-decoration: underline;
      display: inline;
    }

    input {
      display: none;

      + .form__checkbox-mark {
        position: absolute;
        top: 0;
        left: 0;
        content: '';
        width: 20px;
        height: 20px;
        border: 1px solid $lightgrey;
        margin-right: 8px;
        border-radius: 5px;
      }

      &:checked + .form__checkbox-mark {
        border-color: var(--button-primary);
        background-color: var(--button-primary);
        
        &::before {
          position: absolute;
          content: '';
          width: 5px;
          top: 9px;
          left: 4px;
          height: 2px;
          background-color: #fff;
          transform: rotate(45deg);
        }

        &::after {
          width: 10px;
          height: 2px;
          top: 8px;
          left: 6px;
          transform: rotate(-45deg);
          position: absolute;
          content: '';
          background-color: #fff;
        }
      }
    }
  }

  &__button {
    margin-top: 20px;
    font-size: 13px;
    height: 56px;
  }

  &__back {
    display: block;
    text-align: center;
    font-weight: bold;
    margin-top: 20px;
    text-decoration: underline;
    font-size: 14px;
    line-height: 16px;
    color: var(--button-primary);
  }

  &__order {
    margin-bottom: 24px;
    &-title {
      @include fontSize(17px);
      font-weight: 600;
      margin-bottom: 12px;
      @include breakpoint(xl) {
        @include fontSize(16px);
      }
    }
    &__item {
      padding: 5px 17px;
      border-left: 5px solid #F0F3F8;
      @include fontSize(14px);
      &:not(:last-of-type) {
        margin-bottom: 16px;
      }
      &-name {
        font-weight: 600;
      }
      &-model, &-code {
        color: $darkgrey;
        margin-top: 4px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }
      &-price {
        margin-top: 4px;
      }
    }
  }
}

.order-wrapper {
  max-width: 420px;
  width: 100%;
  margin: 40px auto 0;
  @include breakpoint(xl) {
    margin: 20px 0 30px;
    max-width: 100%;
  }
}

.manager-wrapper {
  max-width: 450px;
  width: 100%;
  margin: 40px auto 0;
  @include breakpoint(xl) {
    margin-top: 20px;
    margin: 40px 0;
    max-width: 100%;
  }
}

#__bs_notify__ {
  display: none!important;
}

.hr {
  border-bottom: 1px solid #E1E3E6;
  z-index: 101;
  background: #E1E3E6;
  width: 100%;
  position: absolute;
  bottom: 0;
  height: 1px;
  left: 50%;
  transform: translateX(-50%);
}

.wrap {
  display: flex;
  width: 100%;
  @include breakpoint(xl) {
    flex-direction: column;
  }
  &_banners {
    margin-top: 20px;
    margin-bottom: 20px;

    @include breakpoint(xl) {
      margin: 0;
    }
    > * {
      &:not(:first-of-type) {
        margin-left: 16px;
        @include breakpoint(xl) {
          margin-left: 0;
          margin-top: 16px;
        }
      }
    }
  }
}

.text-darkgray {
  color: $darkgrey;
}

.text-primary {
  color: var(--main-500);
}

.text-secondary {
  color: var(--button-primary);
}

.d-block {
  display: block;
}

.d-inline-block {
  display: inline-block;
}

.description {
  line-height: 20px;
  margin-top: 40px;
  @include breakpoint(xl) {
    margin-top: 20px;
  }
}

.icon-circle {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 24px;
  background-color: #F0F3F8;
  border-radius: 100%;
  width: 100px;
  height: 100px;
  .icon {
    height: 55px;
  }
}


.style {
  &__product-page {
    .header__sub, .search, .sidebar {
      @include breakpoint(xl) {
        display: none;
      }
    }
  }
  &__product-page {
    .header__sub, .search, .sidebar {
      @include breakpoint(xl) {
        display: none;
      }
    }
  }
}

