.form__checkbox {
  user-select: none;
  display: flex;
  align-items: center;
  position: relative;
  min-height: 20px;
  text-align: left;
}
.form__checkbox label {
  padding-left: 28px;
  z-index: 1;
  cursor: pointer;
}
.form__checkbox a {
  text-decoration: underline;
  display: inline;
}
.form__checkbox input {
  display: none;
}
.form__checkbox input + .form__checkbox-mark {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 20px;
  height: 20px;
  border: 1px solid #E1E3E6;
  margin-right: 8px;
  border-radius: 5px;
}
.form__checkbox input:checked + .form__checkbox-mark {
  border-color: var(--button-primary);
  background-color: var(--button-primary);
}
.form__checkbox input:checked + .form__checkbox-mark::before {
  position: absolute;
  content: "";
  width: 5px;
  top: 9px;
  left: 4px;
  height: 2px;
  background-color: #fff;
  transform: rotate(45deg);
}
.form__checkbox input:checked + .form__checkbox-mark::after {
  width: 10px;
  height: 2px;
  top: 8px;
  left: 6px;
  transform: rotate(-45deg);
  position: absolute;
  content: "";
  background-color: #fff;
}
