.header__cart-popup {
  z-index: 99;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s;
  cursor: default;
  position: absolute;
  width: 420px;
  background: #fff;
  top: 130%;
  border: 1px solid #E1E3E6;
  border-radius: 5px;
  box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1);
  right: 0;
  display: flex;
  flex-direction: column;
  padding: 17px 20px 24px;
  text-align: center;
  color: #141414;
}
.header__cart-popup::before {
  content: "";
  position: absolute;
  top: -6px;
  right: 60px;
  width: 12px;
  height: 12px;
  background-color: #fff;
  transform: rotate(45deg);
  border: 1px solid #E1E3E6;
}
.header__cart-popup::after {
  content: "";
  position: absolute;
  top: 0px;
  right: 57px;
  width: 20px;
  height: 12px;
  background-color: #fff;
}
.header__cart-popup p {
  margin: 0 0 16px;
  line-height: 18px;
}
.header__cart-popup .icon-circle {
  width: 68px;
  height: 68px;
  margin-bottom: 16px;
  position: relative;
}
.header__cart-popup .icon-circle .icon-circle__count {
  position: absolute;
  top: 0;
  right: 0;
  width: 17px;
  height: 17px;
  color: #fff;
  font-size: 0.8333333333rem;
  font-weight: bold;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--button-primary);
}
.header__cart-popup .icon-circle .icon {
  width: 30px;
  height: 32px;
  margin: 0;
}
.header__cart-popup.active {
  visibility: visible;
  opacity: 1;
}
@media screen and (max-device-width: 1199px) {
  .header__cart-popup {
    right: auto;
    left: 50%;
    transform: translateX(-50%);
    width: calc(100vw - 30px);
  }
  .header__cart-popup::before {
    right: auto;
    left: 50%;
    transform: translateX(-50%) rotate(45deg);
  }
  .header__cart-popup::after {
    right: auto;
    left: 50%;
    transform: translateX(-50%);
  }
}
